<template lang="pug">
.resultBlock
    el-popover(:placement='offset<10?"right":"left"' :title='getDatetime(offset)' :width='300' trigger='click' :hide-after='0')
        el-row.content-container(align="middle")
            el-col.content-label(:span="2") 未派案
            el-col(:span="22")
                el-row(v-for="(row,i) in unassignList" align="middle")
                    el-col.content-idx(:span="2") {{i+1}}
                    el-col.content(:span="22")
                        .content-title {{row}}
                        div 無資料
        el-row.content-container(align="middle")
            el-col.content-label(:span="2") 已派案
            el-col(:span="22")
                el-row(v-for="(row,i) in assignList" align="middle")
                    el-col.content-idx(:span="2" style="line-height: 70px" :class="getColor(addOffset(row.first_consultation_date))") {{i+1}}
                    el-col.content(:span="22" style="height: 70px;" :class="getColor(addOffset(row.first_consultation_date))")
                        .content-title(style="color: #F0F2F5;") {{row.counselor_name}}
                        div(style="color: #F0F2F5;")  {{row.name}} {{row.caseId}}<br>諮商次數：{{addOffset(row.first_consultation_date)}}
        template(#reference='')
            .block-container(v-if="unassignList.length<=0")
                //- .full-block 已滿
                .divide-block-colored(v-for="(item,i) in assignList" v-show="i<showNum" :style="assignList.length>showNum?'width: '+100/showNum+'%':'width: '+100/assignList.length+'%'" :class="getColor(addOffset(item.first_consultation_date))") {{item.counselor_name}}
                .more-block(v-if="assignList.length>showNum") +{{assignList.length-showNum}}
            //- 全為未派案
            .block-container(v-else-if="assignList<=0")
                .divide-block(v-for="(item,i) in unassignList" v-show="i<showNum" :style="unassignList.length>showNum?'width: '+100/showNum+'%':'width: '+100/unassignList.length+'%'") {{item}}
                .more-block(v-if="unassignList.length>showNum") +{{unassignList.length-showNum}}
            //- 皆有
            .block-container(v-else)
                .divide-block(v-for="(item,i) in unassignList" v-show="i<showNum" :style="unassignList.length>showNum?'width: '+100/showNum+'0%':'width: '+100/unassignList.length+'%'") {{item}}
                .more-block(v-if="unassignList.length>showNum") +{{unassignList.length-showNum+assignList.length}}
                .more-block(v-else) +{{assignList.length}}

</template>

<script>
import {weektimeMixin} from "@/components/weektimeMixin"
export default {
    mixins: [weektimeMixin],
    props: ['results','offset'],
    data() {
        return {
            unassignList: [],
            assignList: [],
            showNum: 3
        }
    },
    mounted() {
        for(var result in this.results){
            if(this.results[result] == 'unassign') {
                this.unassignList.push(result)
            }
            else{
                if(this.checkRangeDay(this.offset,this.results[result].end_consultation_date,this.results[result].first_consultation_date,this.results[result].status)) {
                    let resultData = this.results[result]
                    this.assignList.push(resultData)
                }
                else{
                    this.unassignList.push(result)
                }
            }
        }
    },
    methods: {
        getColor(time) {
            if(time >= 15) {
                return 'purple-block'
            }
            else if(time >= 7) {
                return 'red-block'
            }
            else if(time >= 0) {
                return 'blue-block'
            }
            else {
                return 'gray-block'
            }
        },
        getDatetime(offset){
            var weekdays = ["（日）","（ㄧ）","（二）","（三）","（四）","（五）","（六）"]
            var today = new Date()
            today.setDate(today.getDate() + offset)
            var date = today.getFullYear()+' / '+(today.getMonth()+1)+' / '+today.getDate()+'  '+weekdays[today.getDay()]
            return date
        },
        addOffset(startdate) {
            var today = new Date()
            today.setDate(today.getDate() + this.offset)
            return this.countConsultationNumber(today, startdate)
        }
    }
}
</script>
<style lang="sass" scoped>
@import "@/style/common.sass"
.resultBlock
    height: calc( 100% - 2px )
    cursor: pointer
.content-container
    border: 1px solid $GrayBorder
    margin: 10px 0px
.content-label
    padding: 5px
.content-idx
    line-height: 50px
    text-align: center
    background-color: $GrayBorder
    color: $PrimaryText
    border-width: 0 !important
.content
    height: 50px
    padding: 5px
    border: 1px solid $GrayBorder
    border-width: 1px 1px 1px 0 !important
.content-title
    line-height: 1.4em
    color: $PrimaryText
    font-weight: bold
.block-container
    height: 100%
    display: flex
    align-items: center
.full-block
    width: 100%
    padding: 10px
    background: linear-gradient(90deg, rgb(159, 229, 151), rgb(204, 229, 129))
    font-weight: bold
    border-radius: 8px
    text-align: center
@mixin resultBox()
    display: flex
    align-items: center
    height: calc( 100% - 2px )
    text-align: center
    border-radius: 4px
    border: 2px solid $PrimaryText
    border-width: 2px
    margin: 1px
    padding: 0 1px
    line-height: 1.1em
    font-size: 12px
    font-weight: bold
.divide-block
    @include resultBox()
    background-color: #FFF
    border: 1px solid $PrimaryText
.divide-block-colored
    @include resultBox()
    color: $BaseFill
.more-block
    @include resultBox()
    background-color: $PrimaryText
    color: $BaseFill
</style>