<template lang="pug">
.page-container
    el-row
        el-col(:span="12")
            .page-title 派案列表
            select.labelSelector(v-model='semesterLabel' @change='semesterLabelSelected()')
                option(value='') 請選擇或輸入學期標籤
                option(v-for='option in labelOption' :key='option.label' :value='option.label') {{ option.label }}
        el-col(:span="12")
            download-excel(:data='downloadXLS()' :fields="json_fields" :name='"派案列表"+getDateStr(0)+".xls"')
                el-button(type="success" size="medium" style="float: right;margin-top:30px;") 下載Excel
            el-button(type="success" size="medium" style="float: right;margin-top: 30px;margin-right: 10px;" @click="downloadCSV()") 下載CSV
    el-popover(placement="bottom" title="" :width="400" trigger="hover")
        strong 操作方式
        div 點擊時段可以查看更詳細排班情況
        br
        strong 方塊顏色意義
        div 灰色空格：心理師此時段為有空
        div 藍色實心：心理師此時段已有排到個案，並且諮商次數為0~6次
        div 紅色實心：心理師此時段已有排到個案，並且諮商次數為7~14次
        div 紫色實心：心理師此時段已有排到個案，並且諮商次數為15次以上
        template(#reference)
            span.timetable-info 操作說明
                InfoFilled(style="width: 1em; height: 1em; padding: 0 0 0 3px")
    .result-table
        .result-aside
            div(style="width: 100px;height: 1.3em;font-size: 0.9em;text-align: center") 時段 \ 日期
            .result-aside-label(v-for="(time,i) in times") {{time.label}}
        .result-table-inner(v-loading="tableLoading")
            .result-header
                .result-header-col(v-for="offset in getWeekAry()" :class="isWeekend(offset)?'strong-row':''") {{!checkOffsetIsHoliday(offset) ? getDatetime(offset) : '假日'}}
                    //- el-tag(v-if="checkOffsetIsHoliday(offset)" type="danger" size="mini" class="mx-1" effect="dark" round) 假日
            .result-body
                .result-body-row(v-for="(time,i) in times")
                    .result-body-col(v-for="offset in getWeekAry()" :class="isWeekend(offset)?'strong-row':''")
                        template(v-if="timetable[offset%7] && !checkOffsetIsHoliday(offset)")
                            resultBlock(v-if="timetable[offset%7].rows[time.value]" :results="timetable[offset%7].rows[time.value].counselors" :offset="offset")

</template>

<script>
import resultBlock from '@/views/assign/resultBlock'
import {weektimeMixin} from "@/components/weektimeMixin"
import _ from "lodash"
export default {
    mixins: [weektimeMixin],
    components: {resultBlock},
    inject: ["reload"],
    data: function () {
        return {
            timetable: {
            },
            dayList: [],
            tableLoading: false,
            json_fields: {
                "姓名": "name",
                "學號": "caseId" ,
                "學期標籤": "semester_label",
                "諮商時段": "consultation_datetime",
                "派案老師": "counselor_name",
                "諮商開始時間": "first_consultation_date",
                "危機程度": "crisis_level",
                "初談老師": "init_counselor_name",
                "狀態說明": "discription",
                "初談時間": "init_consultation_date"
            },
            semesterLabel: '',
            labelOption: []
        }
    },
    computed: {
        counselors() {
            console.log(this.semesterLabel)
            if (this.semesterLabel !== '')
                return this.$store.state.counselors.filter(counselor => counselor.semester_label === this.semesterLabel)
            else
                return this.$store.state.counselors
        },
        assignCases() {
            let cases = _.cloneDeep(this.$store.state.assignCases.data)
            if (this.semesterLabel !== '')
                return cases.filter(item => item.semester_label === this.semesterLabel)
            else
                return cases
        }
    },
    async created() {
        await this.$store.dispatch('fetchHoliday')
        await this.$store.dispatch('fetchSemesterLabel')
        this.labelOption = this.$store.state.semester_label.data
        this.semesterLabel = this.$route.query.semesterLabel ? this.$route.query.semesterLabel : this.labelOption[0].label
        this.tableLoading = true
        await this.$store.dispatch('fetchAssignCases')
        await this.$store.dispatch('fetchCounselors')
        this.createResultTable()
        this.tableLoading = false
        console.log(this.timetable)
    },
    methods: {
        semesterLabelSelected() {
            this.$router.push({ path: '', query: { semesterLabel: this.semesterLabel } })
            this.reload()
        },
        createResultTable() {
            console.log(this.counselors)
            this.timetable = {}
                // set week days
            for(let offset=0 ; offset<=7 ; offset++) {
                var weekdays = ["U","M","T","W","R","F","S"]
                var today = new Date()
                today.setDate(today.getDate() + offset)
                const offset_date = weekdays[today.getDay()]
                if (offset_date != "U" && offset_date!= "S") {
                    // add dayList
                    this.dayList.push({name: offset_date,offset: offset})
                    this.timetable[offset] = {rows: {}}
                    // set counselors
                    this.counselors.forEach(counselor => {  // get counselors in the day
                        let availableTimeAry = counselor.available_time.split(',')
                        availableTimeAry.forEach(availableTime => {
                            if(availableTime.indexOf(offset_date) >= 0) {
                                if(!this.timetable[offset].rows[availableTime.charAt(1)])
                                    this.timetable[offset].rows[availableTime.charAt(1)] = {counselors: {}}
                                this.timetable[offset].rows[availableTime.charAt(1)].counselors[counselor.name] = 'unassign'
                            }
                        })
                    })
                }
            }
            this.assignCases.forEach(assingCase => {
                var day = this.dayList.find(e => {return e.name == assingCase.consultation_day})
                if (this.timetable[day.offset].rows[assingCase.consultation_time]) {
                    this.timetable[day.offset].rows[assingCase.consultation_time].counselors[assingCase.counselor_name] = assingCase
                }
            })
        },
        isWeekend(offset){
            var today = new Date()
            today.setDate(today.getDate() + offset)
            if(today.getDay()==5) return true
            return false
        },
        getDatetime(offset){
            var weekdays = ["（日）","（ㄧ）","（二）","（三）","（四）","（五）","（六）"]
            var today = new Date()
            today.setDate(today.getDate() + offset)
            var date = (today.getMonth()+1)+' / '+today.getDate()+'  '+weekdays[today.getDay()]
            return date
        },
        getDateStr(offset){
            var today = new Date()
            today.setDate(today.getDate() + offset)
            return today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()
        },
        getWeekAry(){
            var ary = []
            var today = new Date()
            for(let i=1;i<85;i++) {
                var the_day = new Date()
                the_day.setDate(today.getDate() + i)
                if(the_day.getDay()!=0 && the_day.getDay()!=6){
                    ary.push(i)
                }
            }
            return ary
        },
        downloadXLS() {
            let cases = _.cloneDeep(this.assignCases)
            for( let i = 0; i < cases.length; i++) {
                cases[i].consultation_datetime = '星期'+this.days.find(e => e.value == cases[i].consultation_day).label+' '+this.times.find(e => e.value == cases[i].consultation_time).label
            }
            return cases
        },
        downloadCSV() {
            const cases = _.cloneDeep(this.assignCases);
            const formattedCases = this.convertCases(cases)
            const csvContent = this.buildCSVContent(formattedCases)
            const encodedUri = encodeURI(csvContent)
            window.open(encodedUri)
        },
        formatConsultationDateTime(caseItem) {
            console.log(caseItem);
            const dayMatch = this.days.find(e => e.value == caseItem.consultation_day);
            const timeMatch = this.times.find(e => e.value == caseItem.consultation_time);

            // Check if dayMatch and timeMatch are defined
            const dayLabel = dayMatch ? dayMatch.label : 'Unknown day';
            const timeLabel = timeMatch ? timeMatch.label : 'Unknown time';

            return '星期' + dayLabel + ' ' + timeLabel;
        },
        convertCases(cases) {
            return cases.map(caseItem => ({
                ...caseItem,
                consultation_datetime: this.formatConsultationDateTime(caseItem),
                init_consultation_date: this.convertUTCtoStr(caseItem.init_consultation_date)
            }));
        },
        buildRow(caseItem) {
            return Object.keys(this.json_fields).map(label => caseItem[this.json_fields[label]]);
        },

        buildCSVContent(cases) {
            const rows = [Object.keys(this.json_fields)];
            cases.forEach(caseItem => {
                rows.push(this.buildRow(caseItem));
            });
            return "data:text/csv;charset=utf-8," + rows.map(row => row.join(",")).join("\r\n");
        },
        checkOffsetIsHoliday(offset) {
            let today = new Date()
            today.setDate(today.getDate() + offset)
            return this.checkHoliday(today)
        }
    }
}
</script>
